@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@300;500;700;900&family=Pixelify+Sans:wght@500;600&display=swap');

body {
  margin: 0;
  font-family: 'Noto Serif JP', serif-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
